div.app {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 90%;
  min-height: 100vh;
  background-color: #ddd;
  color: #222;
}

header {
  flex-basis: 30vh;
}

main {
  display: flex;
  flex-grow: 1;
  padding: 30px 0;
  color: rgb(15, 15, 15);
}

aside {
  min-width: 140px;
}

section.page {
  flex-grow: 1;
}

footer {
  background-color: #000;
  color: #fff;
  text-align: center;
  padding: 10px 0;
  text-transform: uppercase;
}